@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.container {
  margin: auto;
  height: 100vh;
  max-width: 800px;
  background: rgb(7, 4, 56);
  background: linear-gradient(151deg, rgba(7, 4, 56, 1) 0%, rgba(15, 15, 106, 1) 44%, rgba(45, 164, 189, 1) 100%);
}

header {
  display: flex;
  align-items: flex-end;
  height: 100px;
}
h1 {
  font-family: "Lato", sans-serif;
  font-size: 24px;
  font-weight: 600;
  text-align: center;
  height: 25px;
  width: 300px;
  padding: 10px;
  border-radius: 0 5px 30px 0;
  background-color: #b6e457;
}

section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 150px;
}
.cercle {
  border-radius: 50%;
  height: 80px;
  width: 80px;
  background-color: #b6e457;
}
.logo {
  width: 80px;
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
}

.currencyBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border-radius: 5px;
  background-color: rgb(241, 243, 244, 0.9);
  width: 40%;
  height: 200px;
}

.equal {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-align: center;
  width: 5%;
  font-size: 20px;
  font-weight: bold;
  color: white;
}

input[type="text"] {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 20px;
  text-align: center;
  outline: none;
  border: none;
  border-radius: 5px;
  height: 40px;
  width: 80%;
  background-color: #b6e457;
}

select {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 18px;
  width: 80%;
  padding: 10px 20px;
  outline: none;
  border: none;
  border-radius: 5px;
  background-color: #e8eaed;
}

/* Classe ajoutée ou enlevée en cas d'erreur de saisie */
.red {
  background-color: rgb(248, 148, 148) !important;
}

